import clsx from 'clsx';
import React, { useRef, useImperativeHandle, forwardRef, useMemo } from 'react';
import { HeroDefaultProps, HeroPropTypes } from './generated/propTypes';
import HeroOverlay from './HeroOverlay';

const Hero = forwardRef((props, externalRef) => {
    const {
        alignment,
        children,
        className,
        imageUrl,
        style,
        themeColor,
        overlay,
        overlayProps,
        ...customProps
    } = props;

    const ref = useRef();
    useImperativeHandle(externalRef, () => ref.current, []);

    const classNames = useMemo(
        () =>
            clsx(
                'lmnt lmnt-hero',
                themeColor && `lmnt-theme-${themeColor}-bg`,
                `lmnt-hero__align-${alignment}`,
                className
            ),
        [alignment, className, themeColor]
    );

    const styles = useMemo(() => {
        if (!imageUrl) return style;
        return {
            ...style,
            backgroundImage: `url(${imageUrl})`
        };
    }, [imageUrl, style]);

    return (
        <div ref={ref} className={classNames} style={styles} {...customProps}>
            {overlay && <HeroOverlay {...overlayProps} />}
            {children}
        </div>
    );
});

Hero.displayName = 'Hero';
Hero.propTypes = HeroPropTypes;
Hero.defaultProps = HeroDefaultProps;

export default Hero;
