import clsx from 'clsx';
import { Group } from '@element/react-group';
import { Typography } from '@element/react-typography';
import React, { useRef, useImperativeHandle, forwardRef, useMemo } from 'react';
import {
    HeroContentDefaultProps,
    HeroContentPropTypes
} from './generated/propTypes';

const HeroContent = forwardRef((props, externalRef) => {
    const {
        children,
        className,
        heroBtn,
        logo,
        overline,
        padding,
        title,
        style,
        subtitle,
        textColor,
        ...customProps
    } = props;

    const ref = useRef();

    const classNames = useMemo(
        () =>
            clsx(
                'lmnt lmnt-hero-content',
                `lmnt-theme-${textColor}`,
                className
            ),
        [className, textColor]
    );

    useImperativeHandle(externalRef, () => ref.current, []);
    const styles = useMemo(
        () => ({
            ...style,
            padding
        }),
        [padding, style]
    );
    return (
        <div ref={ref} className={classNames} {...customProps} style={styles}>
            {children || (
                <Group gap='dense' direction='vertical' secondaryAlign='center'>
                    {logo && logo}
                    {overline && (
                        <Typography type='overline'>{overline}</Typography>
                    )}
                    {title && <Typography type='display2'>{title}</Typography>}
                    {subtitle && (
                        <Typography type='display6'>{subtitle}</Typography>
                    )}
                    {heroBtn && heroBtn}
                </Group>
            )}
        </div>
    );
});

HeroContent.displayName = 'HeroContent';
HeroContent.propTypes = HeroContentPropTypes;
HeroContent.defaultProps = HeroContentDefaultProps;

export default HeroContent;
