import clsx from 'clsx';
import React, { useRef, useImperativeHandle, forwardRef, useMemo } from 'react';
import {
    HeroOverlayDefaultProps,
    HeroOverlayPropTypes
} from './generated/propTypes';

const HeroOverlay = forwardRef((props, externalRef) => {
    const { children, className, position, overlayThemeColor, ...customProps } =
        props;

    const ref = useRef();

    useImperativeHandle(externalRef, () => ref.current, []);

    const classNames = useMemo(
        () =>
            position !== 'none' &&
            clsx(
                'lmnt',
                'lmnt-hero-overlay ',
                !overlayThemeColor.includes('gradient') &&
                    overlayThemeColor !== 'surface' &&
                    `lmnt-hero-overlay__${overlayThemeColor}`,
                overlayThemeColor.includes('gradient') &&
                    overlayThemeColor !== 'surface' &&
                    `lmnt-theme--bg-${overlayThemeColor}`,
                overlayThemeColor === 'surface' && 'lmnt-theme-surface-bg',
                `lmnt-hero-overlay__${position}`,
                className
            ),
        [overlayThemeColor, position, className]
    );

    return (
        <div className={classNames} {...customProps}>
            {children && children}
        </div>
    );
});

HeroOverlay.displayName = 'HeroOverlay';
HeroOverlay.propTypes = HeroOverlayPropTypes;
HeroOverlay.defaultProps = HeroOverlayDefaultProps;

export default HeroOverlay;
